.vert-move {
  -webkit-animation: mover 1s infinite alternate;
  animation: mover 1s infinite alternate;
}
.vert-move {
  -webkit-animation: mover 1s infinite alternate;
  animation: mover 1s infinite alternate;
}
@-webkit-keyframes mover {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-10px);
  }
}
@keyframes mover {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-10px);
  }
}

*:focus {
  outline: none;
}

.container-card {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  border-radius: 7px;
  height: 170px;
  width: 90%;
  padding-right: 10px;
  margin-right: -90px;
}

.card {
  display: flex;
  justify-content: flex-end;
  background-color: rgba(124, 124, 124, 0);
  transition: 0.2s ease-out;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  width: 120px;
  height: 45px;
  border-radius: 5px;
  align-items: center;
  font-family: "Recursive", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  font-size: 13px;
  margin-left: 90px;
}

.card span {
  margin-right: 14px;
  color: rgba(61, 61, 61, 0.62);
  -webkit-user-select: none;
  cursor: pointer;
}

.card:hover {
  background-color: #eeeeee;
  cursor: pointer;
}

.selected-card {
  background-color: #eeeeee;
  cursor: pointer;
}

.selected-card span {
  margin-right: 33px;
}

.card-text {
  position: absolute;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
}

.moving-left {
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  transform: translate(-1.2em, 0);
  -webkit-transform: translate(-1.2em, 0);
  -moz-transform: translate(-1.2em, 0);
  -o-transform: translate(-1.2em, 0);
  -ms-transform: translate(-1.2em, 0);
}

.first-enter {
  opacity: 0;
  transform: translateY(-1.2em);
}
.first-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 1400ms, transform 1400ms;
}
.first-exit {
  opacity: 1;
}
.first-exit-active {
  opacity: 0;
  transform: translateY(1.2em);
  transition: opacity 300ms, transform 300ms;
}

.blink {
  -webkit-animation: blinker 0.7s step-start infinite;
          animation: blinker 0.7s step-start infinite;
}

@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

